import React, { useRef } from "react"
import styled from "styled-components"
import { useDispatch, useSelector } from "react-redux"
import { cartSelector, emptyCart, closeCart } from "../cartSlice"
import { useOnClickOutside } from "../functions/useOnClickOutside"

const SideBarCart = styled.div`
  position: relative;
  border: 1px solid rebeccapurple;
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  background: #fff;
  width: 250px;
  transform: ${({ open }) => (open ? `translateX(0)` : `translateX(100%)`)};
  transition: all 0.5s ease-in-out;
  span[role="button"] {
    position: absolute;
    top: 15px;
    right: 25px;
  }
`

export default function Cart() {
  const cartItems = useSelector(cartSelector).items
  const open = useSelector(cartSelector).isCartOpen
  const dispatch = useDispatch()
  const ref = useRef()

  useOnClickOutside(ref, () => dispatch(closeCart()))

  return (
    <SideBarCart open={open} ref={ref}>
      <span role="button" onClick={() => dispatch(closeCart())}>
        X
      </span>
      <button onClick={() => dispatch(emptyCart())}>EMPTY REDUX CART</button>
      <button onClick={() => window.localStorage.removeItem("wooCart")}>
        EMPTY LOCALSTORAGE
      </button>
      {!cartItems?.length ? (
        <p>No item in the cart</p>
      ) : (
        cartItems.map(item => (
          <div key={item.product_id}>
            <p>
              {item.name} - {item.price} 
            </p>
            <p>qty: {item.quantity}</p>
            <p>
              <strong>Total: {item.price * item.quantity} </strong>
            </p>
          </div>
        ))
      )}
    </SideBarCart>
  )
}
